.outer-triangle {
  border: 20px solid #0000;
  border-top-width: 0;
  border-bottom: 40px solid #0a0a0a;
  align-items: center;
  width: 0;
  height: 0;
  transition: all 1s ease-out;
  display: flex;
  position: absolute;
  top: -10px;
