  left: -12px;
  transform: rotate(0);
}

@property --gradient-angle {
  syntax: "<angle>";
  inherits: false;
  initial-value: -15deg;
}

.work-pill {
  color: var(--color-text-primary);
  cursor: pointer;
  border-radius: 8px;
  margin: 0 8px 0 5px;
  display: inline-block;
  position: relative;
}

.work-pill:before, .work-pill:after {
  content: "";
  border-radius: inherit;
  background: conic-gradient(from var(--gradient-angle), #292929, #454545, #9a9a9a, #454545, #292929);
  opacity: .4;
  height: 24px;
  transition: opacity .5s;
  position: absolute;
  inset: 11px -5px 0 -1px;
}

.work-pill:hover:after, .work-pill:hover:before {
  opacity: 1;
  animation: 10s linear infinite rotation;
}

.inset-span:after {
  content: "";
  z-index: 1;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background: #1515156b;
  border-radius: 7px;
  height: 22px;
  position: absolute;
  top: 12px;
  left: 0;
  right: -4px;
}

@keyframes rotation {
  0% {
    --gradient-angle: -15deg;
  }

  100% {
    --gradient-angle: 345deg;
  }
}

.work-pill:before {
  filter: blur(8px);
}

.work-pill:after {
