#spline-player-layout-container {
    position: fixed;
    top: 0;
    right: 0;
    padding: var(--main-padding) var(--main-padding) var(--main-padding) 3%;
    height: 100%;
    width: 50%;
    z-index: 3;
    text-align: center;

    @media (max-width: 820px) {
        padding: 0;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        display: none;
    }

}

.spline-player-layout-container-visible {
    display: block !important; 
}

.spline-player-clipping-container {
    height: 100%;
    width: 100%;
    border-radius: 80px;
    overflow: hidden;

    @media (max-width: 820px) {
        border-radius: 0;
    }
}

.spline-player-label {

    text-transform: uppercase;
    font-family: 'ReplicaMonoLLWeb-Regular';
    font-size: 12px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    transform: translateY(-8px);

    div {
        height: 4px;
        width: 4px;
        background-color: rgba($color: #ffffff, $alpha: 0.2);
        border-radius: 2px;
    }

    @media (max-width: 820px) {
        transform: translateY(-76px);
    }
}

#icon-close-spline-viewer {
    display: none;
    top: 32px;
    right: 32px;
    
    @media (max-width: 820px) {
        display:block;
        
    }
}